@use "sass:color";

$background: #0a0e13;
$abyss: #3d1e92;
$midnight-blue: #1c0c5b;
$neon-purple: #6932ff;
$violet: #4923b2;

.button {
  align-items: center;
  color: white;
  cursor: pointer;
  display: flex;
  height: 2.6rem;
  justify-content: center;
  margin: 1.5rem;
  position: relative;
  text-decoration: none;
  width: 19rem;
  z-index: 0;
}

.button:after {
  content: "";
  background: $violet;
  border-radius: 12px;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}

.button:hover:after {
  background: $neon-purple;
  border: thin solid lighten($neon-purple, 5%);
}

.button-text {
  margin-left: 0.5rem;
}

@keyframes glow {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@media only screen and (min-device-width: 992px) {
  .button:before {
    animation: glow 30s linear infinite;
    border-radius: 20px;
    content: "";
    background: linear-gradient(
      45deg,
      $neon-purple,
      $violet,
      $abyss,
      $violet,
      $abyss,
      $midnight-blue,
      $midnight-blue,
      $abyss,
      $violet,
      $abyss,
      $violet,
      $neon-purple
    );
    background-size: 400%;
    filter: blur(5px);
    height: calc(100% + 20px);
    opacity: 0;
    position: absolute;
    transition: opacity 0.3s ease-in-out;
    width: calc(100% + 20px);
    z-index: -1;
  }

  .button:hover:before {
    opacity: 0.7;
  }
}
